
import {
  computed,
  defineComponent,
  onMounted,
  onUnmounted,
  reactive,
} from "vue";
import ButtonLink from "@/components/ui/button-link.vue";
import UiParagraph from "@/components/ui/ui-paragraph.vue";
import UiScreen from "@/components/ui/ui-screen.vue";
import { playCorrect, playTypewriter, playWrong } from "@/utils/audio-manager";

const REQUIRED_SCORE = 4;

interface TortureState {
  locked: boolean;
  displayError: boolean;
  prompt: string;
  typed: string;
  cursor: number;
  score: number;
  free: boolean;
  displayIntro: boolean;
}

const characters = "abcdefghijklmnopqrstuvwxyz0123456789";
export default defineComponent({
  name: "screen-torture",
  props: {
    level: {
      type: Number,
      required: true,
    },
    last: {
      type: Boolean,
    },
  },
  emit: ["exit"],
  components: { ButtonLink, UiParagraph, UiScreen },
  setup(props, context) {
    const state: TortureState = reactive({
      locked: false,
      displayError: false,
      prompt: "",
      typed: "",
      cursor: 0,
      score: 0,
      free: false,
      displayIntro: false,
    });

    const generatePrompt = () => {
      let prompt = "";
      const length = 6 + Math.floor(Math.random() * 5);
      for (let i = 0; i < length; i++) {
        let char = characters.charAt(
          Math.floor(Math.random() * characters.length)
        );
        if (Math.random() > 0.5) {
          char = char.toUpperCase();
        }
        prompt += char;
      }
      state.prompt = prompt;
    };
    generatePrompt();

    const reset = () => {
      state.typed = "";
      state.cursor = 0;
      generatePrompt();
    };

    const onSuccess = () => {
      state.score++;
      state.locked = true;
      playCorrect();

      if (state.score >= REQUIRED_SCORE) {
        state.free = true;
      }

      setTimeout(() => {
        state.locked = false;
        reset();
      }, 300);
    };

    const onError = () => {
      state.score = Math.max(state.score - 1, 0);
      playWrong();

      state.locked = true;
      state.displayError = true;
      setTimeout(() => {
        state.displayError = false;
        state.locked = false;
        reset();
      }, 400);
    };

    const onKeyDown = (e: KeyboardEvent) => {
      if (state.locked) return;

      const pressed = e.key;
      if (!characters.includes(pressed.toLowerCase())) return;
      state.typed += pressed;
      playTypewriter();

      const char = state.prompt.charAt(state.cursor) || "";
      if (char === pressed) {
        state.cursor++;
        if (state.cursor === state.prompt.length) {
          onSuccess();
        }
      } else {
        onError();
      }
    };

    onMounted(() => window.addEventListener("keydown", onKeyDown));
    onUnmounted(() => window.removeEventListener("keydown", onKeyDown));

    function exit(force = false) {
      if (props.level === 0 && !force) {
        state.displayIntro = true;
      } else {
        context.emit("exit");
      }
    }

    return { state, exit };
  },
});
